.contenedor {
    display: flex;
    justify-content: center;
    background: url('/public/images/cuartaG.jpg');
         
  }
  
  .main {
    flex-grow: 1;
    padding: 20px; 
    margin: 0px 12px 0px 60px; 
    text-align: center;
    position: relative;
  }

  .react-player{
    position: absolute;
    padding-bottom: 30;

    padding-right: 50px;
  }
  
  .aside {
    width: 550px; 
    background-color: rgba(58, 76, 240, 0.4);
    padding:20px 50px;

  }
  .aside h2{
    color: aliceblue;
    font-size: 28px;
  }
  .aside li{
    color: aliceblue;
    font-size: 20px;
    padding: 15px 0px;
    font-family: Georgia, Times, 'Times New Roman', serif;
  }

  .contenedor h1{
    color: aliceblue;
    font-size: 40px;
    padding: 15px 0px;
  }
  .contenedor p{
    color: aliceblue;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 30px;
    background-color: rgba(58, 76, 240, 0.4);
    padding: 10px; 
  }



  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }
  